<template>
	<div>
		<p style="cursor: pointer; color: #18aeff;" @click="showDrawer"> 详情 </p>
		<a-drawer title="用户资料" :width="700" :visible="visible" :body-style="{ paddingBottom: '80px' }" @close="onClose">
			<a-form-model :label-col="labelCol" :wrapper-col="wrapperCol">
				<div style="display: flex; justify-content: space-around;">
					<div>
						<a-form-model-item label="用户名">
							<a-input v-model="record.name" :disabled="true" class="user_inputField" />
						</a-form-model-item>
						<a-form-model-item label="性别">
							<a-select v-model="record.gender" :disabled="!modify" class="user_inputField">
								<a-select-option value="M">男</a-select-option>
								<a-select-option value="F">女</a-select-option>
							</a-select>
						</a-form-model-item>
						<a-form-model-item label="年龄">
							<a-select :default-value="ageList[record.age]" @change="handleAgeChange" :disabled="!modify" class="user_inputField">
								<a-select-option :value=key v-for="(value, key) in ageList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-model-item>
						<a-form-model-item label="职业">
							<a-select :default-value="occupationList[record.occupation]" @change="handleOccupationChange" :disabled="!modify" class="user_inputField">
								<a-select-option :value=key v-for="(value, key) in occupationList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-model-item>
						<a-form-model-item label="邮编">
							<a-input v-model="record.zipCode" :disabled="!modify" class="user_inputField" />
						</a-form-model-item>
					</div>
					<a-form-model-item label="">
						<a-upload name="file" list-type="picture-card" class="poster-uploader-userDetail" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
						 :before-upload="beforeUpload" @change="handleChange">
							<img v-if="imageUrl" :src="imageUrl" style="width: 200px; height: 240px;" />
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-model-item>
				</div>
				<div>
					<a-form-model-item label="修改开关">
						<a-switch v-model="modify" style="margin-left: 10px;" />
					</a-form-model-item>
					<a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
						<a-button type="primary" @click="onSubmit" :disabled="!modify">
							确定
						</a-button>
						<a-button style="margin-left: 30px;" @click="onClose">
							返回
						</a-button>
					</a-form-model-item>
				</div>
			</a-form-model>
		</a-drawer>
	</div>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath', 'picRequestPath'])
		},
		data() {
			return {
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},

				picloading: false,
				modify: false,
				visible: false,
				imageUrl: '',
				latestImageUrl: null,
			};
		},
		props: ['record', 'ageList', 'occupationList'],
		async created() {
			this.imageUrl = this.picRequestPath + 'headpic/' + this.record.headPic;
			this.latestImageUrl = this.imageUrl;
		},
		watch: {
			record: async function(obj) {
				this.imageUrl = this.picRequestPath + 'headpic/' + this.record.headPic;
				this.latestImageUrl = this.imageUrl;
			}
		},
		methods: {
			showDrawer() {
				this.visible = true;
			},
			onClose() {
				this.visible = false;
				this.modify = false;
				this.record.headPic = this.latestImageUrl;
				this.imageUrl = this.latestImageUrl;
			},
			
			handleAgeChange(value){
				this.record.age = value;
			},
			handleOccupationChange(value){
				this.record.occupation = value;
			},
			
			async onSubmit() {
				let {
					data
				} = await this.axios.post(this.requestPath + 'modifyUser', this.record);
				if (data.code == 0) {
					this.$message.success('修改成功');
				} else {
					this.$message.error('修改失败');
				}
				this.onClose();
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.record.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	};
</script>
<style lang='scss'>
	.user_inputField {
		margin-left: 20px;
		width: 200px;
	}

	.selectorField {
		margin-left: 20px;
		width: 500px;
	}

	.poster-uploader-userDetail>.ant-upload {
		width: 200px;
		height: 240px;
	}

	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}

	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
